.container {
    max-width: 1200px;
    margin: 0 auto;
    overflow: hidden;
    padding: 0 15px;
}

//sticky footer
body {
    display: flex;
    min-height: 100vh;
    flex-direction: column;
}

.main-content {
    flex: 1;
}

.hover {
        color: $color-white;
        transition: 0.3s;
        &:hover {
            color: $color-white;
            text-decoration: none;
            opacity: 0.5;
        }
}